import { getFirebase } from '../lib/firebase';
import create from 'zustand';
import { subscribeWithSelector } from 'zustand/middleware';
import { navigate } from 'gatsby';
const USERS_DB = 'profiles';

const firebase = getFirebase();
const db = firebase.firestore();

firebase
    .auth()
    .setPersistence(
        typeof window !== 'undefined'
            ? firebase.auth.Auth.Persistence.LOCAL
            : firebase.auth.Auth.Persistence.NONE
    );

let userStateSubscription = null;
export const useUserStore = create(
    subscribeWithSelector((set) => ({
        user: null,
        loading: true,
        initUser: (async () => {
            firebase.auth().onAuthStateChanged(async (user) => {
                if (user) {
                    const extraUserData = await db.collection(USERS_DB).doc(user.uid).get();
                    set({ user: { ...user.toJSON(), ...extraUserData.data() }, loading: false });
                } else {
                    set({ user: null, loading: false });
                }

                if (!userStateSubscription) {
                    userStateSubscription = useUserStore.subscribe(
                        (state) => state.user,
                        (user) => {
                            if (user) {
                                navigate('/dashboard');
                            } else {
                                navigate('/log-in');
                            }
                        }
                    );
                }
            });
        })(),
        createUserWithEmailAndPassword: async (email, password, extraData) => {
            try {
                await firebase.auth().createUserWithEmailAndPassword(email, password);
                const authUser = firebase.auth().currentUser;
                const user = await db
                    .collection(USERS_DB)
                    .doc(authUser.uid)
                    .set({
                        ...extraData,
                    });

                set({ user: { ...authUser.toJSON(), ...user } });
            } catch (err) {
                throw err;
            }
        },
        logInWithEmailAndPassword: async (email, password) => {
            try {
                await firebase.auth().signInWithEmailAndPassword(email, password);
                const authUser = firebase.auth().currentUser;
                const user = await db.collection(USERS_DB).doc(authUser.uid).get();

                set({ user: { ...authUser.toJSON(), ...user.data() } });
            } catch (err) {
                console.log(err);
                throw err;
            }
        },
        createGoogleAccount: async (extraData) => {
            try {
                const provider = new firebase.auth.GoogleAuthProvider();
                await firebase.auth().signInWithPopup(provider);

                const authUser = firebase.auth().currentUser;
                await db
                    .collection(USERS_DB)
                    .doc(authUser.uid)
                    .set({
                        ...extraData,
                    });

                const user = await db.collection(USERS_DB).doc(authUser.uid).get();
                const fullUser = { ...authUser.toJSON(), ...user.data() };
                set({ user: fullUser });
            } catch (err) {}
        },
        logOut: async () => {
            return await firebase.auth().signOut();
        },
    }))
);
